@-webkit-keyframes rotate-1 {
    
    0% {
        -webkit-transform-origin: center 60%;
                transform-origin: center 60%;
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg); 
        
    }
    100% {
        -webkit-transform-origin: center 60%;
                transform-origin: center 60%;
        -webkit-transform: rotate(180deg);
                transform: rotate(180deg);
    }
}


@keyframes rotate-1 {
    
    0% {
        -webkit-transform-origin: center 60%;
                transform-origin: center 60%;
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg); 
        
    }
    100% {
        -webkit-transform-origin: center 60%;
                transform-origin: center 60%;
        -webkit-transform: rotate(180deg);
                transform: rotate(180deg);
    }
}

