html {
    font-size: 24px;
    line-height: 1.15;
    

    --ac-black: #000;
    --ac-gray: #151515;
    --ac-gray-translucent: #151515e7;
    --ac-dark-gray: #0d0d0d;
    --ac-dark-gray-translucent: #0d0d0de7;
    --ac-light-gray: lightgray;
    --ac-white: #fff;
    
    --ac-red: #BF0000;
    --ac-red-dark: #370000;
    
    --ac-blue: #0000BF;
    --ac-blue-dark: #000037;

    --ac-green: #00BF00;
    --ac-green-dark: #003700;

    --ac-signature-blue: #4299ff;

    --ac-text-gray: #71757a;

    --ac-primary: #bf0000;

    --rgb-red: #ff0000;
    --rgb-blue: #0000ff;
}


@media(max-width: 15800px) {
    html {
        font-size: 20px;
        line-height: 1.15;
        
    }
}

@media(max-width: 980px) {
    html {
        line-height: 1.1;
        font-size: 14px;
    }
}

@media(max-width: 800px) {
    html {
        line-height: 1.0;
        font-size: 14px;
    }
}
.button{
    border-radius: 40px;
    padding: 15px 30px;
    border: none;
    outline: none;
    font-size: 1rem;
    vertical-align: top;
    cursor: pointer;
    text-decoration: none;
    text-align: center;
    -webkit-transition: -webkit-transform 250ms;
    transition: -webkit-transform 250ms;
    transition: transform 250ms;
    transition: transform 250ms, -webkit-transform 250ms;
    -webkit-transition: background-color 550ms;
    transition: background-color 550ms;
    -webkit-transition: color 550ms;
    transition: color 550ms;
}

.button:hover{
    background-color: azure;
    color: black;
    border: 3px solid #ff5722;
    padding: 12px 27px;

}

.button:active{
    -webkit-transform: scale(0.92);
            transform: scale(0.92);
}

.gray-text {
  color: var(--ac-text-gray);
}
.red-text {
  color: var(--ac-primary);
}



